import React from 'react';
import {Button, Typography, Grid} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import HomeIcon from '@material-ui/icons/Home';

const useStyles = makeStyles(theme =>({
    root: {
        flexGrow: 1,
        overflowX: 'hidden',
        overflowY: 'hidden',
        [theme.breakpoints.down("xs")]: {    
            marginTop: 50,  
        },
        [theme.breakpoints.up("sm")]: {
            marginTop: 50,
        },
        [theme.breakpoints.up("md")]: {
            marginTop: 70,
        },
        [theme.breakpoints.up("lg")]: {
            marginTop: 120,       
        },
        [theme.breakpoints.up("xl")]: {
            marginTop: 150,
        },
    },
    header: {
        fontWeight: 'bold',
    },
    content: {
        fontStyle: 'oblique'
    },
    Btn: {
        background: '#0099ee',
        borderRadius: 20,
        padding: '.5em 1.5em .5em 1.5em',
        color: 'white',
        "&:hover": {
            backgroundColor: '#0080ff'
        }
    }
}));

function Error404() {

    const classes = useStyles();

    return (
        <div className={classes.root}>
            <Grid container spacing={2} direction="row" justify="center" alignItems="center">
                <Grid item xs={10} sm={10} md={10} lg={12} xl={12}>
                    <Typography variant="h1" align="center" color="textPrimary" paragraph gutterBottom>
                            <span className={classes.header}>Oops!</span>
                    </Typography>
                </Grid>
                <Grid item xs={10} sm={10} md={12} lg={12} xl={12}>
                    <Typography className={classes.header} variant="h4" align="center" color="error" paragraph gutterBottom>
                        HTTP Error 404 <span style={{color: 'black', fontWeight: 'normal'}}>- Page Not Found</span>
                    </Typography>
                    
                </Grid>
                <Grid item xs={10} sm={10} md={12} lg={12} xl={12}>
                    <Typography className={classes.content} variant="subtitle1" align="center" color="textPrimary">
                        The Page you are looking for might have been removed,
                        had its name changed, or is temporarily unavailable.
                    </Typography>
                </Grid>
                <Grid item xs={10} sm={10} md={12} lg={12} xl={12}>
                    <Typography className={classes.content} variant="subtitle1" align="center" color="textPrimary" paragraph gutterBottom>
                        If you typed the page address in the Address bar, make sure that it is spelled correctly.
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
                    <Typography variant="h1" align="center">
                        <Button className={classes.Btn} startIcon={<HomeIcon />} href="/">HOMEPAGE</Button>
                    </Typography>
                </Grid>
            </Grid>
        </div>
    )
}

export default Error404;
