import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Grid from '@material-ui/core/Grid';
import AppBarCollapse from './AppBarCollapse'
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import backgroundImageM from '../../../resources/images/home-header-dark.jpg';
import Logo from '../../../resources/images/IZ-Logo.png';
import HeaderPresentation2 from '../Presentation/HeaderPresentation2';
import Link from '@material-ui/core/Link';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    overflowX: 'hidden',
    display: 'flex',
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
  paperContainer: {
    width: '100%',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center center',
    [theme.breakpoints.down("xs")]: {
      height: 300,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundImage: `url(${backgroundImageM})`
    },
    [theme.breakpoints.up("sm")]: {
      height: 300,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center center',
      backgroundImage: `url(${backgroundImageM})`
    },
    [theme.breakpoints.up("md")]: {
      height: 300,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center center',
      backgroundImage: `url(${backgroundImageM})`
    },
    [theme.breakpoints.up("lg")]: {
      height: 400,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center center',
      backgroundImage: `url(${backgroundImageM})`
    },
    [theme.breakpoints.up("xl")]: {
      height: 500,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center center',
      backgroundImage: `url(${backgroundImageM})`
    },  
    backgroundImage: `url(${backgroundImageM})`
  },
  appBarBottom: {
    borderBottom: "1px solid #00d8ff"
},
}));


 function AppBar_2() {
  const classes = useStyles();

  return (
    <div id="Home" className={classes.root}>
      <Paper className={classes.paperContainer}>
        <AppBar position="fixed"  className={classes.appBarBottom} color="default">
          <Toolbar>
              <Grid container direction="row" justify="center" alignItems="center" >
                  <Grid item xs sm md lg>
                        <Link href="/" underline="none" color="textPrimary">
                            <Avatar className={classes.large} alt="IZ Logo" src={Logo} />
                        </Link>                       
                  </Grid>  
              </Grid>
              <Grid container direction="row" justify="center" alignItems="center" >
                <AppBarCollapse />              
              </Grid>             
          </Toolbar>
        </AppBar>
        <HeaderPresentation2 />  
      </Paper>
    </div>
  );
}

export default AppBar_2