import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import MyQR from '../../../resources/images/MyQR2.png';
import ContactMe from '../../../resources/images/contact-us.svg';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';

const useStyles = makeStyles((theme) => ({
    MyQR: {
        [theme.breakpoints.down("xs")]: {    
            height: '30vmin',
            width: '30vmin', 
        },
        [theme.breakpoints.up("sm")]: {
            height: '35vmin',
            width: '35vmin'
        },
        [theme.breakpoints.up("md")]: {
            height: '20vmin',
            width: '20vmin',
        },
        [theme.breakpoints.up("lg")]: {
            height: '25vmin',
            width: '25vmin',       
        },
        [theme.breakpoints.up("xl")]: {
            height: '25vmin',
            width: '25vmin',
        },
    },
    myIMG: {
        [theme.breakpoints.down("xs")]: {    
            height: '35vmin',
            width: '35vmin', 
        },
        [theme.breakpoints.up("sm")]: {
            height: '37vmin',
            width: '37vmin'
        },
        [theme.breakpoints.up("md")]: {
            height: '25vmin',
            width: '25vmin',
        },
        [theme.breakpoints.up("lg")]: {
            height: '30vmin',
            width: '30vmin',       
        },
        [theme.breakpoints.up("xl")]: {
            height: '30vmin',
            width: '30vmin',
        },
    },
    title: {
        textAlign: 'center',
    }
}));

function DialogQR(props) {
    const classes = useStyles();       
    return (
        <div>
            <Dialog
                open={props.openDialog}
                onClose={props.closeDialog}
                aria-labelledby="responsive-dialog-title">

                <DialogTitle id="responsive-dialog-title" className={classes.title}>
                    <strong>
                        Personal Contact
                    </strong>
                </DialogTitle>
                <DialogContent dividers>
                    <DialogContentText>
                        <Grid container direction="row" justify="center" alignItems="center">      
                            <Grid item xs={7} sm={7} md={7} lg={7} xl={7}>
                                <img src={ContactMe} className={classes.myIMG} alt="Contact Me" />
                            </Grid>
                            <Grid item xs={5} sm={5} md={5} lg={5} xl={5}>
                                <img src={MyQR} className={classes.MyQR} alt="QR Code" />
                            </Grid>
                        </Grid>
                        
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={props.closeDialog} color="primary">
                        Close
                    </Button>
                </DialogActions>
            </Dialog> 
        </div>          
    )
}

export default DialogQR
